.about-nav {
    width: 105em;
    box-sizing: border-box;

    background-color: rgba(202, 156, 240, 0.596);
    display: flex;
    justify-content: center;
}

.sky {
    height: 30%;
    float: left;
    width: 50%;
    display: inline;
    background-color: #0f0;
}

.pages {
    width: 105em;
    background-color: blueviolet;
}

.summary {
  font-size: 2.5em;
  object-fit: contain;
  float: right;
  justify-content: right;
  width: 41%;
  height: 900px;

  border: 10px solid rgba(0, 0, 0, 0.356) ;
    padding: 35px;
    position: relative;
    top: 100px;
}
.form {
    background-color: #9778c9;
    width: 50em;
    height: 5em;
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    align-items: center;

    position:absolute;
    left: 45%;
    top: 13%;
  }

.input {
    all: unset;
    font: 25px system-ui;
    color: #fff;
    height: 100%;
    width: 100%;
    padding: 6px 10px;
  }
  
  .name-icon {
    float: right;
    position: relative;
    right: 50px;
    top: 50px;
  }

  .container {
    float: right;
    justify-content: right;

    
  }